ty */\n      }\n/* Modal Content (Image) */\n.modal-content {\n        margin: auto;\n        display: block;\n        width: 80%;\n        max-width: 700px;\n      }\n/* Caption of Modal Image (Image Text) - Same Width as the Image */\n#caption {\n        margin: auto;\n        display: block;\n        width: 80%;\n        max-width: 700px;\n        text-align: center;\n        color: #ccc;\n        padding: 10px 0;\n        height: 150px;\n      }\n/* Add Animation - Zoom in the Modal */\n.modal-content, #caption {\n        -webkit-animation-name: zoom;\n                animation-name: zoom;\n        -webkit-animation-duration: 0.6s;\n                animation-duration: 0.6s;\n      }\n@-webkit-keyframes zoom {\n        from {transform:scale(0)}\n        to {transform:scale(1)}\n      }\n@keyframes zoom {\n        from {transform:scale(0)}\n        to {transform:scale(1)}\n      }\n/* The Close Button */\n#closeModal {\n        position: absolute;\n        top: 15px;\n        right: 35px;\n        color: #f1f1f1;\n        font-size: 40px;\n        font-weight: bold;\n        transition: 0.3s;\n      }\n#closeModal:hover,\n      #closeModal:focus {\n        color: #bbb;\n        text-decoration: none;\n        cursor: pointer;\n      }\n/* 100% Image Width on Smaller Screens */\n@media only screen and (max-width: 700px){\n        .modal-content {\n          width: 100%;\n        }\n      }\n/*# sourceMappingURL=data:application/json;base64, */"